import React from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'

// Material UI components
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import Chip from '@material-ui/core/Chip'
import Container from '@material-ui/core/Container'
import Drawer from '@material-ui/core/Drawer'
import FormGroup from '@material-ui/core/FormGroup'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Grid from '@material-ui/core/Grid'
import Input from '@material-ui/core/Input'
import InputAdornment from '@material-ui/core/InputAdornment'
import Icon from '@material-ui/core/Icon'
import IconButton from '@material-ui/core/IconButton'
import InputLabel from '@material-ui/core/InputLabel'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Paper from '@material-ui/core/Paper'
import Select from '@material-ui/core/Select'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'

// Material UI Styles
import { ThemeProvider } from '@material-ui/styles'
import { makeStyles } from '@material-ui/core/styles'
import { themeHQ } from '../../../styles/theme.ts'

// Material Icons
import AddIcon from '@material-ui/icons/AddRounded'
import DropdownIcon from '@material-ui/icons/ArrowDropDownRounded'
import CheckIcon from '@material-ui/icons/CheckCircleRounded'
import CloseIcon from '@material-ui/icons/CloseRounded'
import DeleteIcon from '@material-ui/icons/DeleteRounded'
import EditIcon from '@material-ui/icons/EditRounded'
import MoreHorizIcon from '@material-ui/icons/MoreHorizRounded'
import VisibilityOnIcon from '@material-ui/icons/VisibilityRounded'
import VisibilityOffIcon from '@material-ui/icons/VisibilityOffRounded'

// Gastby
import { Link } from 'gatsby'
import withRoot from '../../../withRoot'

// components
import TopNav from '../../../components/hq/hqTopNav'
import SettingsSideNav from '../../../components/hq/settingsSideNav'

const useStyles = makeStyles(theme => ({
  root: {
    // ...theme.typography.button,
    // paddingTop: themeHQ.spacing(6),
    // paddingLeft: themeHQ.spacing(6),
    overflowX: 'auto',
    backgroundColor: themeHQ.palette.background.paper,
    minHeight: 800
  },

  section: {
    backgroundColor: themeHQ.palette.background.paper
  },

  panel: {
    // marginTop: 50,
    width: 480,
    height: '100%'
  },

  panelContainer: {
    height: 'inherit'
  },

  panelBody: {
    flexGrow: 1
  },

  sideNav: {
    height: '100%',
    backgroundColor: '#2F2F4F'

  }
}))

function DataImport ({ location }) {
  const classes = useStyles()

  const [state, setState] = React.useState({
    checkboxTouchId: false,
    checkboxTouchIdEdit: false,
    edit: false,
    editDeviceLock: false
  })

  const tasks = [
    createTask('Customer', 'customer_02.csv', '2,753', '19', 'Sep 21, 2019'),
    createTask('Customer', 'customer_01.csv', '3,000', '17', 'Sep 17, 2019'),
    createTask('Customer', 'custom_01.csv', '0/3,000', 'N/A', 'Sep 17, 2019'),
    createTask('Product', 'product.csv', '17,937', '83', 'Sep 16, 2019'),
    createTask('Store', 'store.csv', '87', '0', 'Sep 16, 2019')
  ]

  function createTask (c1, c2, c3, c4, c5) {
    const cb1 = randomBolean()
    const cb2 = randomBolean()
    const cb3 = randomBolean()
    console.log('cb1 = ' + cb1)
    console.log('cb2 = ' + cb2)
    console.log('cb3 = ' + cb3)
    return { c1, c2, c3, c4, c5, cb1, cb2, cb3 }
  }

  function randomBolean () {
    const cb = (Math.random() < 0.5)
    return cb
  }

  const [anchorEl, setAnchorEl] = React.useState(null)
  const [dropdownTags, setDropdownTags] = React.useState(null)

  function handleClick (event) {
    setAnchorEl(event.currentTarget)
  }

  function handleClose () {
    setAnchorEl(null)
  }

  function handleClickDropdownTags (event) {
    setDropdownTags(event.currentTarget)
  }

  function handleCloseDropdownTags () {
    setDropdownTags(null)
  }

  const [values, setValues] = React.useState({
    name: 'Columbus Circle',
    age: '',
    multiline: 'Controlled',
    currency: 'EUR',
    password: '',
    showPassword: false,
    loyaltyTier: '',
    customerList: ''
  })

  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value })
  }

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(+event.target.value)
    setPage(0)
  }

  const toggleDrawer = (item, open) => event => {
    state.checkboxTouchIdEdit = state.checkboxTouchId
    setState({ ...state, [item]: open })
  }

  const saveEdit = (item, open) => event => {
    state.checkboxTouchId = state.checkboxTouchIdEdit
    setState({ ...state, [item]: open })
  }

  const handleChangeCheck = name => event => {
    setState({ ...state, [name]: event.target.checked })
  }

  const editPanel = item => (
    <Paper className={classes.panel}>
      <Grid container direction='column' className={classes.panelContainer}>
        <Grid item>
          <Box pt={6} p={2} borderBottom={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='space-between' alignItems='center'>
            <Box>
              <Typography variant='h3' display='block'> Assign Phone Number </Typography>
              <Typography variant='caption' display='block'> Columbus Circle </Typography>
            </Box>
            <IconButton aria-label="Close" onClick={toggleDrawer('edit', false)}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </Box>
        </Grid>
        <Grid item className={classes.panelBody}>
          <Box p={2}>
            <form className={classes.container} noValidate autoComplete="off">
              <FormControl margin='normal' fullWidth='true'>
                <InputLabel htmlFor="loyalty-tier">Loyalty Tiers</InputLabel>
                <Select
                  IconComponent={DropdownIcon}
                  value={values.loyaltyTier}
                  onChange={handleChange('loyaltyTier')}
                  inputProps={{
                    name: 'loyalty-tier',
                    id: 'loyalty-tier'
                  }}
                >
                  <MenuItem value={10}>Bronze</MenuItem>
                  <MenuItem value='Diamond'>Diamond</MenuItem>
                  <MenuItem value={30}>Gold</MenuItem>
                  <MenuItem value={40}>Silver</MenuItem>
                  <MenuItem value={50}>VIP</MenuItem>
                </Select>
              </FormControl>
              <FormControl margin='normal' fullWidth='true'>
                <InputLabel htmlFor="customer-list">Customer List</InputLabel>
                <Select
                  IconComponent={DropdownIcon}
                  value={values.customerList}
                  onChange={handleChange('customerList')}
                  inputProps={{
                    name: 'customer-list',
                    id: 'customer-list'
                  }}
                >
                  <MenuItem value='Black Book'>Black Book</MenuItem>
                  <MenuItem value={20}>Store Book</MenuItem>
                </Select>
              </FormControl>
            </form>

            <Box hidden= {values.customerList !== 'Black Book' || values.loyaltyTier !== 'Diamond'} bgcolor='secondary.light' color='text.primary' p={2} mb={3} borderRadius='4px'>
              <Typography variant='body1'>There are restrictions already added for the selected loyalty tier and customer list</Typography>
              <Typography align='right' ><Button color="primary" variant='text' size='small' > Review and Update </Button></Typography>
            </Box>

            {/* <FormGroup row>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={state.checkedA}
                    onChange={handleChangeCheck('checkedA')}
                    value="checkedA"
                    color='primary'/>
                }
                label="Allow app user to unlock app with Touch ID"
              />
            </FormGroup>
            <FormHelperText hidden={!state.checkedA}>By default, unlock with Touch ID is disabled. App users need to manually enable it in App Security Settings. </FormHelperText>
            <FormHelperText>Unlock with Touch ID on shared device is not recommended. </FormHelperText> */}
          </Box>
        </Grid>
        <Grid item>
          <Box p={2} borderTop={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='flex-end'>
            <Box mr={2}><Button variant='outlined' color='secondary' onClick={toggleDrawer('edit', false) }>Cancel</Button></Box>
            <Button variant='contained' color='primary' onClick={toggleDrawer('edit', false)}>Save</Button>
          </Box>
        </Grid>
      </Grid>
    </Paper>
  )

  const editDeviceLockPanel = item => (
    <Paper className={classes.panel}>
      <Grid container direction='column' className={classes.panelContainer}>
        <Grid item>
          <Box pt={7} pb={1} p={2} borderBottom={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='space-between' alignItems='center'>
            <Box>
              <Typography variant='h3' display='block'> FTP Upload Settings </Typography>
              {/* <Typography variant='caption' display='block'> Columbus Circle </Typography> */}
            </Box>
            <IconButton aria-label="Close" onClick={toggleDrawer('editDeviceLock', false)}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </Box>
        </Grid>
        <Grid item className={classes.panelBody}>
          <Box p={2}>
            <FormGroup row>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={state.checkboxTouchIdEdit}
                    onChange={handleChangeCheck('checkboxTouchIdEdit')}
                    value="checkboxTouchIdEdit"
                    color='primary'/>
                }
                label="Upload from FTP folder"
              />
            </FormGroup>

            {/* <Box hidden={!state.checkboxTouchIdEdit} bgcolor='secondary.light' color='text.primary' p={2} mt={2} borderRadius='4px'>
              <Typography variant='body2'>By default, unlock with Touch ID is disabled in mobile apps. Unlock with Touch ID on shared device is not recommended. </Typography>
              <Typography variant='body2'> </Typography>
            </Box> */}
          </Box>
        </Grid>
        <Grid item>
          <Box p={2} borderTop={1} borderColor={themeHQ.palette.divider} display='flex' justifyContent='flex-end'>
            <Box mr={2}><Button variant='outlined' color='secondary' onClick={toggleDrawer('editDeviceLock', false) }>Cancel</Button></Box>
            <Button variant='contained' color='primary' onClick={saveEdit('editDeviceLock', false)}>Save</Button>
          </Box>
        </Grid>
      </Grid>
    </Paper>
  )

  return (
    <div className={classes.root}>
      <ThemeProvider theme={themeHQ}>
        <TopNav />
        <Grid container direction='row'>
          <Grid item md={2}>
            <SettingsSideNav />
          </Grid>
          <Grid item md={10}>
            <Grid container direction='column' >
              <Box p={6} pb={0}>
                <Typography variant='h1' display='block' align='left'>Data Import</Typography>
                <Grid container direction='row' spacing={3} xs={12}>
                  <Grid item md={6}>
                    <Box display='flex' alignItems='center' justifyContent='space-between'>
                      <Typography variant='h3' display='inline'> FTP Upload</Typography>
                      <IconButton aria-label="Edit Device Lock" onClick={toggleDrawer('editDeviceLock', true)} color='primary'>
                        <EditIcon fontSize="small" />
                      </IconButton>
                    </Box>
                    <Paper elevation='0' className={'setting-card'}>
                      <Typography variant='caption'> Upload from FTP folder </Typography>
                      <Typography variant='body1'> {state.checkboxTouchId ? 'Enabled' : 'Disabled' }</Typography>
                    </Paper>
                  </Grid>
                  {/* <Grid item md={6}>
                    <Box display='flex' alignItems='center' justifyContent='space-between'>
                      <Typography variant='h3' display='inline'> Sample Settings </Typography>
                      <IconButton aria-label="Edit" onClick={toggleDrawer('edit', true)} color='primary'>
                        <EditIcon fontSize="small"/>
                      </IconButton>
                    </Box>
                    <Paper elevation='0' className={'setting-card'}>
                      <Box borderBottom='solid 1px #ebebee' pb={2} mb={2}>
                        <Typography variant='caption'> Label </Typography>
                        <Typography variant='body1'> Value </Typography>
                      </Box>
                      <Box>
                        <Typography variant='caption'> Label </Typography>
                        <Typography variant='body1'> Value </Typography>
                      </Box>
                    </Paper>
                  </Grid> */}
                </Grid>
              </Box>
              <Box p={6} pt={0}>
                <Typography variant='h3' gutterBottom className={classes.sectionHeader}>Upload Jobs</Typography>
                <Table minWidth='640'>
                  <TableHead>
                    <TableRow>
                      <TableCell>Data Model</TableCell>
                      <TableCell align="left">File Name</TableCell>
                      <TableCell align="left"># of Records</TableCell>
                      <TableCell align="left"># of Errors</TableCell>
                      <TableCell align="left">Created on</TableCell>
                      <TableCell align="left">Status</TableCell>
                      <TableCell align="right">Action</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {tasks.map(task => (
                      <TableRow key={task.c1} hover>
                        <TableCell component="th" scope="row">
                          <Typography>{task.c1}</Typography>
                        </TableCell>
                        <TableCell align="left">
                          {task.c2 != null && <Typography noWrap maxWidth='240'>{task.c2}</Typography> }
                        </TableCell>
                        <TableCell align="left">
                          {task.c3 != null && <Typography>{task.c3}</Typography> }
                        </TableCell>
                        <TableCell align="left">
                          <Typography>{task.c4}</Typography>
                        </TableCell>
                        <TableCell align="left">
                          <Typography>{task.c5}</Typography>
                        </TableCell>
                        <TableCell align="left">
                          {/* <Chip hidden={task.cb1} label="Default Tag" /> */}
                          <Chip hidden={!task.cb2} label="completed" color='primary' />
                          {/* <Chip hidden={task.cb3} label="Failed" color='secondary'/> */}
                        </TableCell>
                        {/* <TableCell align="right">{row.c4}</TableCell> */}
                        <TableCell align="right">
                          <IconButton aria-label="more" aria-controls="table-tags-menu" aria-haspopup="true" onClick={handleClickDropdownTags}>
                            <MoreHorizIcon fontSize="small" />
                          </IconButton>
                          <Menu
                            id="table-tags-menu"
                            anchorEl={dropdownTags}
                            keepMounted
                            open={Boolean(dropdownTags)}
                            onClose={handleCloseDropdownTags}
                          >
                            <MenuItem onClick={toggleDrawer('edit', true)}>Edit</MenuItem>
                            <MenuItem onClick={handleCloseDropdownTags}>Duplicate</MenuItem>
                            <MenuItem onClick={handleCloseDropdownTags} disabled>Disable</MenuItem>
                          </Menu>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
                <TablePagination
                  rowsPerPageOptions={[5, 10, 25]}
                  component="div"
                  count={rows.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  backIconButtonProps={{
                    'aria-label': 'previous page'
                  }}
                  nextIconButtonProps={{
                    'aria-label': 'next page'
                  }}
                  onChangePage={handleChangePage}
                  onChangeRowsPerPage={handleChangeRowsPerPage}
                />
                <Drawer variant='persistent' anchor="right" open={state.edit} onClose={toggleDrawer('edit', false)}>
                  {editPanel('edit')}
                </Drawer>
              </Box>
            </Grid>
          </Grid>
        </Grid>
        <Drawer variant='temporary' anchor="right" open={state.editDeviceLock} onClose={toggleDrawer('editDeviceLock', false)}>
          {editDeviceLockPanel('editDeviceLock')}
        </Drawer>
      </ThemeProvider>
    </div>
  )
}

DataImport.propTypes = {
  classes: PropTypes.object.isRequired
}

// export default withRoot(withStyles(styles)(About));
export default withRoot(DataImport)
